import {
  assignAdminNavbar,
  fetchUrl,
  formatCurrency,
  getDateFromTimestamp,
  getToken,
  logoutBtnOnClick,
  redirectTo,
  reloadDelay,
} from "../util";
import { ROLES, monthsArr } from "../util/constants";
import {
  XANO_ADMIN_SIGN_IN_REDIRECT,
  XANO_SUPER_ADMIN_ALL_FINANCE_DETAIL_URL,
  XANO_SUPER_ADMIN_FINANCE_DETAIL_NEW_ENTRY_URL,
} from "../util/xano_urls";

// ? prevent 'global' variables from being accessed and overwritten in google inspect console
(() => {
  let UrlMonth, UrlYear;

  window.onload = async function () {
    logoutBtnOnClick("/admin/sign-in", "sidebar-logout-btn");

    var curUrl = new URL(document.location.href);
    UrlMonth = curUrl.searchParams.get("month") || null;
    UrlYear = curUrl.searchParams.get("year") || null;

    if (!UrlMonth || !UrlYear) {
      // ? TODO: Change this to a modal showcasing the error
      console.error("No month or year found");
      return;
    }

    const token = getToken();
    if (token) {
      const xano_input = {
        month: UrlMonth,
        year: UrlYear,
      };

      let result = await fetchUrl(XANO_SUPER_ADMIN_ALL_FINANCE_DETAIL_URL, "POST", xano_input, token);

      if (result.isFetchOk !== undefined && !result.isFetchOk) {
        console.error(result.message);
        if (
          result.payload &&
          result.payload.role === ROLES.ADMIN &&
          result.payload.gyms &&
          result.payload.gyms.length > 0
        )
          redirectTo(`/admin/gym/g?gymCode=${result.payload.gyms[0].gym_id}`);
        else redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
        return;
      }

      assignAdminNavbar(ROLES.SUPER_ADMIN);
      getSuperAdminData(result, token);
    } else {
      redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
    }
  };

  function getSuperAdminData(_data) {
    const { finance, external_finance, payment_ledger } = _data;

    // ? add external_finance to finance via month+year
    const result = {};
    finance.map((f) => {
      if (result[f.month + "," + f.year] !== undefined) {
        return (result[f.month + "," + f.year] = {
          ...result[f.month + "," + f.year],
          additional_revenue: f.additional_revenue + result[f.month + "," + f.year].additional_revenue,
          additional_expenses: f.additional_expenses + result[f.month + "," + f.year].additional_expenses,
          gross_revenue: f.gross_revenue + result[f.month + "," + f.year].gross_revenue,
          member_revenue: f.member_revenue + result[f.month + "," + f.year].member_revenue,
          fixed_expense: f.fixed_expense + result[f.month + "," + f.year].fixed_expense,
          net_profit: f.net_profit + result[f.month + "," + f.year].net_profit,
        });
      } else {
        return (result[f.month + "," + f.year] = f);
      }
    });

    const mergedArr = result;
    external_finance.map((f) => {
      if (mergedArr[f.month + "," + f.year] !== undefined) {
        return (mergedArr[f.month + "," + f.year] = {
          ...mergedArr[f.month + "," + f.year],
          additional_revenue: f.credit + mergedArr[f.month + "," + f.year].additional_revenue,
          additional_expenses: f.debit + mergedArr[f.month + "," + f.year].additional_expenses,
        });
      } else {
        return f;
      }
    });
    initNewEntry();
    appendFinanceData(mergedArr);

    appendFinanceTable([...finance.map((f) => f.additional_entries)], payment_ledger, external_finance);
  }

  function appendFinanceData(finances) {
    const { month, year, member_revenue, fixed_expense, gross_revenue, additional_revenue, additional_expenses } =
      finances[UrlMonth + "," + UrlYear];

    const netProfit = Number(gross_revenue) + Number(additional_revenue) - Number(additional_expenses);

    document.getElementById("finance-month").innerText = monthsArr[month - 1];
    document.getElementById("finance-year").innerText = year;
    document.getElementById("financial-member-revenue").innerText = formatCurrency(member_revenue) || "0";
    document.getElementById("financial-fixed-expense").innerText = formatCurrency(fixed_expense) || "0";
    document.getElementById("financial-gross-revenue").innerText = formatCurrency(gross_revenue) || "0";
    document.getElementById("financial-additional-revenue").innerText = formatCurrency(additional_revenue) || "0";
    document.getElementById("financial-additional-expenses").innerText = formatCurrency(additional_expenses) || "0";
    document.getElementById("financial-net-profit").innerText = formatCurrency(netProfit) || "0";
  }

  function appendFinanceTable(_additional_entries = [], payment_ledger = [], external_finance = []) {
    const tableContainer = document.getElementById("account-table-container");
    const og_webflow_element = tableContainer.querySelector("#account-table-item");

    let additional_entries = _additional_entries.flat(1);

    const normalizedPaymentLedger = [
      ...payment_ledger.map((p) => ({
        time: p.time,
        description: `${p.name}  (${p.mode_of_payment} | ${getDateFromTimestamp(p.membership_end_date)})`,
        debit: null,
        credit: p.amount,
        user: true,
      })),
    ];

    const normalizedExternalPaymentLedger = [
      ...external_finance.map((p) => ({
        time: p.created_at,
        description: p.description,
        debit: p.debit,
        credit: p.credit,
      })),
    ];

    let mergedArr = additional_entries.concat(normalizedPaymentLedger);
    mergedArr = mergedArr.concat(normalizedExternalPaymentLedger);

    mergedArr.sort((a, b) => {
      return b.time - a.time;
    });

    mergedArr?.forEach((entry, indx) => {
      const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
      clonedCard.setAttribute("id", "");
      let el = clonedCard.querySelectorAll(".colcontent");

      if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";

      // if (entry && entry.user) clonedCard.style.borderLeft = "2px solid black"

      el[0].innerText = getDateFromTimestamp(entry.time);
      el[1].innerText = entry.description || "";
      el[2].innerText = entry.debit || "";
      el[3].innerText = entry.credit || "";

      tableContainer.append(clonedCard);
    });

    og_webflow_element.remove();
  }

  function initNewEntry() {
    const formBlock = document.getElementById("entry-form-block");
    const newEntryBtn = formBlock.querySelector("#entry-btn");

    newEntryBtn.onclick = () => {
      if (newEntryBtn.disabled) return;
      newEntryBtn.disabled = true;
      newEntryBtn.value = "Submitting...";
      if (!newEntryBtn.classList.contains("disabled")) newEntryBtn.classList.add("disabled");

      submitNewEntry();
    };
  }

  async function submitNewEntry() {
    const $form = $("#entry-form-block");
    if ($form.attr("id") === "entry-form-block") {
      const token = getToken();
      if (token) {
        const description = $form[0][0].value || "";
        const debit = $form[0][1].value || "";
        const credit = $form[0][2].value || "";

        if (description !== "" && (debit !== "" || credit !== "")) {
          const xano_input = {
            month: UrlMonth,
            year: UrlYear,
            description,
            debit,
            credit,
          };

          let result = await fetchUrl(XANO_SUPER_ADMIN_FINANCE_DETAIL_NEW_ENTRY_URL, "POST", xano_input, token);

          if (result.isFetchOk !== undefined && !result.isFetchOk) {
            console.error(result.message);
            // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);

            $form
              .hide() // optional hiding of form
              .siblings(".w-form-done")
              .hide() // Hide failure
              .siblings(".w-form-fail")
              .show() // Show success
              .text("Failed to add new entry. Please try again - Error: " + result.message);

            return;
          }

          $form
            .hide() // optional hiding of form
            .siblings(".w-form-fail")
            .hide() // Hide failure
            .siblings(".w-form-done")
            .show() // Show success
            .text("Successfully added new entry!");

          reloadDelay();
        }
      }
    }
  }
})();

var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */

  $("form").on("change", function () {
    const $form = $(this); // The submitted form
    if ($form.attr("id") === "entry-form-block") {
      const description = $form[0][0].value || "";
      const debit = $form[0][1].value || "";
      const credit = $form[0][2].value || "";

      const formBlock = document.getElementById("entry-form-block");
      const newEntryBtn = formBlock.querySelector("#entry-btn");

      if (description !== "" && (debit !== "" || credit !== "")) {
        newEntryBtn.disabled = false;
        newEntryBtn.classList.remove("disabled");
      } else {
        newEntryBtn.disabled = true;
        if (!newEntryBtn.classList.contains("disabled")) newEntryBtn.classList.addClass("disabled");
      }
    }
  });

  $("form").on("submit", async function (e) {
    e.preventDefault();
  });
});
